html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding-inline: 1.5625rem;
  padding-block: 147px 85px;
  margin: 0;
  font-size: 0.75rem;
  font-weight: 400;
  height: 100vh;
  background-image: linear-gradient(var(--soft-violet), var(--soft-blue));
  display: grid;
  justify-content: center;
  align-content: center;
}
@media (min-width: 71.875em) {
  body {
    padding: 0;
  }
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --vry-dk-d-blue: hsl(238, 29%, 16%);
  --soft-red: hsl(14, 88%, 65%);
  --soft-violet: hsl(273, 75%, 66%);
  --soft-blue: hsl(240, 73%, 65%);
  --vry-dk-gray-blue: hsl(237, 12%, 33%);
  --dk-gray-blue: hsl(240, 6%, 50%);
  --lt-gray-blue: hsl(240, 5%, 91%);
}

:root {
  --font-body: "Kumbh Sans", sans-serif;
}

.container {
  max-width: 60.125rem;
  margin: 0 auto;
  border-radius: 1.875rem;
  padding-inline: 1.5625rem;
  padding-bottom: 4.25rem;
  width: 23.4375rem;
  background-color: var(--white);
}
@media (min-width: 71.875em) {
  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 60.125rem;
    padding: 0;
  }
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

main {
  position: relative;
}
@media (min-width: 71.875em) {
  main::before {
    content: "";
    display: block;
    width: 13rem;
    height: 12rem;
    background-image: url("../images/illustration-box-desktop.svg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center center;
    position: absolute;
    top: 60%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }
}

.background-img {
  position: relative;
  height: 8.5rem;
  width: 100%;
  padding-bottom: 2rem;
}
.background-img::before {
  content: "";
  background-image: url("../images/bg-pattern-mobile.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  width: 100%;
  height: 100%;
  display: block;
}
.background-img::after {
  content: "";
  position: absolute;
  top: -6.875rem;
  left: 0;
  height: 11.4375rem;
  background-image: url("../images/illustration-woman-online-mobile.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  width: 100%;
  display: block;
}
@media (min-width: 71.875em) {
  .background-img {
    padding: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
  .background-img::before {
    content: "";
    background-image: url("../images/bg-pattern-desktop.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100% 100%;
    position: absolute;
    height: 200%;
    left: -550px;
    top: -63%;
    width: 200%;
  }
  .background-img::after {
    content: "";
    background-image: url("../images/illustration-woman-online-desktop.svg");
    background-size: 100% 100%;
    height: 22.5rem;
    position: absolute;
    top: 50%;
    transform: translate(-15%, -50%);
  }
}

@media (min-width: 71.875em) {
  .faq {
    padding-right: 5.9375rem;
    padding-block: 4.01875rem;
  }
}
.faq h1 {
  color: var(--vry-dk-d-blue);
  text-align: center;
  font-size: 1.6875rem;
}
@media (min-width: 71.875em) {
  .faq h1 {
    text-align: left;
  }
}
.faq details {
  border-bottom: 0.0625rem solid var(--lt-gray-blue);
  padding-block: 1.25rem;
  color: var(--dk-gray-blue);
}
.faq details[open] > summary {
  padding-bottom: 1.125rem;
  font-weight: 700;
}
.faq details[open] > summary::before {
  transform: scaleY(-1);
}
.faq details summary {
  color: var(--vry-dk-gray-blue);
  position: relative;
  list-style: none;
  transition: all 200ms ease-in-out;
}
.faq details summary:hover {
  cursor: pointer;
  color: var(--soft-red);
}
.faq details summary::before {
  content: "";
  position: absolute;
  right: 0;
  display: block;
  background-image: url("../images/icon-arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 0.625rem 0.375rem;
  color: var(--soft-red);
  height: 0.95rem;
  width: 0.75rem;
}